
.search-input{height:45px;width:600px;margin:0px auto;margin-top:10px;position:relative
}
.search-input input{border:1px solid #e4e4e4;box-sizing:border-box;width:500px;height:45px;font-size:18px;float:left;padding-left:10px;padding-right:10px;overflow:hidden
}
.search-input input::-ms-clear{display:none
}
.search-input .search-reset{width:21px;height:21px;position:absolute;display:block;line-height:21px;text-align:center;cursor:pointer;font-size:20px;right:110px;top:12px
}
.search-input .search-btn{height:45px;width:100px;border:1px solid mediumseagreen;background-color:mediumseagreen;color:white;font-size:16px;font-weight:bold;float:left;cursor:pointer
}
.search-input .search-select{position:absolute;top:45px;width:500px;box-sizing:border-box;z-index:999
}
.search-input .search-select ul{margin:0;text-align:left
}
.search-input .search-select ul li{border:1px solid #d4d4d4;border-top:none;border-bottom:none;background-color:#fff;width:100%;list-style:none
}
.search-input .search-select ul .search-select-option{box-sizing:border-box;padding:7px 10px
}
.search-input .search-select ul .search-select-list{transition:all 0.5s
}
.search-input .search-select .itemfade-enter,.search-input .search-select .itemfade-leave-active{opacity:0
}
.search-input .search-select .itemfade-leave-active{position:absolute
}
.search-input [v-cloak]{display:none
}



